<template>
  <div class="app-container">
    <div class="filter-container">
      <div class="filter-item" style="margin-right:20px">
        <el-date-picker clearable v-model="listQuery.createTime" type="date"></el-date-picker>
      </div>
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="search">查询</el-button>
    </div>
    <div  class="article-table">
      <el-table
        v-loading="listLoading"
        :data="dataList"
        fit
        :show-header="false"
        style="width: 100%"
      >
        <el-table-column width="130px" align="center" label="创建时间">
          <template slot-scope="scope">
            <el-tag type="warning">
              <i class="el-icon-time"></i>
              <span>{{ scope.row.createTime | parseTime('{y}-{m}-{d}') }}</span>
            </el-tag>
          </template>
        </el-table-column>

        <el-table-column width="30px" align="center">
          <template slot-scope="scope">
            <div class="article-timeline"></div>
          </template>
        </el-table-column>

        <el-table-column width="120px" align="left" label="姓名">
          <template slot-scope="scope">
            <el-tag>{{ scope.row.author }}</el-tag>
          </template>
        </el-table-column>

        <el-table-column width="700px" align="center" label="内容">
          <template slot-scope="scope">
            <el-card class="box-card">
               <div slot="header" class="clearfix">
                   <span style="float:left">
                       {{`云课堂${scope.row.tag}需求文档`}}
                        <el-tag v-if="scope.row.type=='0'" type="success">新增需求</el-tag>
                        <el-tag v-if="scope.row.type=='1'" type="warning">需求变更</el-tag>
                        <el-tag v-if="scope.row.type=='2'" type="danger">临时需求</el-tag>
                   </span>
                   
                    <el-button type='primary' @click="changeDemand(scope.row)" style="float: right;" size="mini">变更</el-button>
                </div> 
              <div class="article-content" v-html="scope.row.content"></div>
            </el-card>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination
      style="margin-top:0px;"
      v-show="total>10"
      :total="total"
      :page.sync="listQuery.page"
      :limit.sync="listQuery.limit"
      @pagination="getList"
    />
  </div>
</template>

<script>
import { Project } from "@/api/project";
import Pagination from "@/components/Pagination"; // Secondary package based on el-pagination
import { mapGetters } from "vuex";
export default {
  name: "ArticleList",
  components: { Pagination },
  computed: {
    ...mapGetters(["name", "userId"])
  },
  data() {
    return {
      currentName: null,
      dataList: [],
      total: 0,
      listLoading: true,
      listQuery: {
        createTime: "",
        page: 1,
        limit: 10
      },
    };
  },
  created() {
    this.getList();
  },
  methods: {
    search() {
      this.listQuery.page = 1;
      this.getList();
    },
    getList() {
      this.listLoading = true;
      let params = JSON.parse(JSON.stringify(this.listQuery));
      Project.getDemand(params).then(response => {
        this.dataList = response.data;
        this.total = response.total;
        this.listLoading = false;
      });
    },
    changeDemand(obj){
        window.localStorage.setItem('demand',JSON.stringify(obj));
        this.$router.push({
            path:'/project/createDemand',
            query:{
                change:1
            }
        })
    },
    handleSizeChange(val) {
      this.listQuery.limit = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    }
  }
};
</script>


